.card {
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px;
    background-color: #fff;
  }
  
  .card-header {
    font-weight: bold;
  }
  
  .priority-icon {
    margin-right: 5px;
  }
  
  .no-priority {
    border-left: 5px solid #ccc;
  }
  
  .low {
    border-left: 5px solid #28a745;
  }
  
  .medium {
    border-left: 5px solid #ffc107;
  }
  
  .high {
    border-left: 5px solid #dc3545;
  }
  
  .urgent {
    border-left: 5px solid #b21f2d;
  }
  