.column {
    background-color: #fff;
  }
  
  .column-header {
    color: #343a40;
  }
  
  .card-list {
    margin: 10px 0;
    min-height: 100px;
    display: flex;
    flex-direction: column;
  }
  
  .add-card-button {
    color: #007bff;
    cursor: pointer;
  }
  
  .add-card-button:hover {
    text-decoration: underline;
  }
  